@mixin select {
 &__select,
 &-select {
  display: flex;
  align-items: center;
  max-width: 640px;
  width: 100%;

  .ant-select-selection-placeholder {
   background: #edf0fd !important;
   padding-left: 11px !important;
  }

  .ant-select-single.ant-select-show-arrow .ant-select-selection-item, .ant-select-single.ant-select-show-arrow .ant-select-selection-placeholder {
   background: #edf0fd !important;
   border-radius: 20px !important;
   padding: 8px 20px !important;
  }

  &-function {
   padding: 17px 0;
   margin-left: 30px;
  }

  .ant-select {
   font-size: 18px;
   line-height: 26px;

   .ant-select-selector {
    background: #edf0fd;
    border-radius: 20px;
    padding-right: 100px;

    .ant-select-selection-placeholder {
     color: #232526;
     background: #edf0fd;
    }
   }

   .ant-select-arrow {
    font-size: 16px;
    margin-right: 10px;
   }
  }
 }
}

@mixin caseLibrary {
 margin: 0 60px;

 &-title {
  font-size: 66px;
  line-height: 54px;
  margin-bottom: 30px;
 }

 &-filter {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 65px;

  &__text {
   max-width: 430px;
   width: 100%;
   font-size: 20px;
   line-height: 30px;
   color: #a3a8bb;
  }

  @include select;

 }


 &-cards {
  display: flex;
  flex-basis: calc(40% - 40px);
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 170px;

  &__btn {
   width: 100%;
   margin: 20px 0 0;
   display: flex;

   .ant-btn {
    margin: 0 auto;
    padding: 0 94px;
   }
  }

  .ant-card {
   border-radius: 40px;
   margin-bottom: 70px;
  }
 }
}
