.account {
 margin: 100px 60px 200px;
 &__top {
  display: flex;
  align-items: center;
  margin-bottom: 120px;
  &-avatar {
   position: relative;
   &:before {
    content: '';
    background-image: url('../../common/images/account/mask-avatar.png');
    position: absolute;
    left: 0;
    width: 150px;
    height: 150px;
   }
   .avatar {
    position: absolute;
    left: 50px;
    height: 150px;
    width: 150px;
    margin: 0;
    padding: 50px 21px;
    border-radius: 50%;
   }
  }
 }

 &__main {
  display: flex;
  align-items: flex-start;
  &-menu {
   font-size: 32px;
   line-height: 40px;
   color: #a3a8bb;
   &__list {
    cursor: pointer;
    margin-bottom: 14px;
   }
  }
  &-wrapper {
   margin-left: 190px;
   width: 100%;
  }
 }


 .card {
  max-width: 990px;
  width: 100%;
  background: #fff;
  box-shadow: 0 8px 20px 6px rgba(35, 81, 255, 0.06);
  border-radius: 40px;
  padding: 40px 30px 40px 50px;
  font-size: 28px;
  line-height: 38px;
 }
}
