.my-cases {
 max-width: 990px;
 width: 100%;
}

.case {
 background: #fff;
 box-shadow: 0 4px 14px 2px rgba(35, 81, 255, 0.06);
 border-radius: 40px;
 padding: 40px 30px 30px 60px;
 margin-bottom: 30px;

 &-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 990px;
  width: 100%;

  &__title {
   max-width: 830px;
   width: 100%;
   font-size: 36px;
   line-height: 50px;
  }

  &__arrow {
   background: #edf0fd;
   border-radius: 110px;
   height: 70px;

   .arrow {
    &-up {
     padding: 28px 23px 29px;
    }

    &-down {
     padding: 30px 23px 27px;
    }
   }
  }
 }

 &-feedback {
  &__title {
   font-size: 18px;
   line-height: 24px;
   color: #a3a8bb;
   margin-bottom: 30px;
  }
 }

}

.feedback {
 max-width: 880px;
 width: 100%;
 margin-bottom: 30px;

 & + .feedback {
  padding-top: 30px;
  border-top: 1px solid #a3a8bb;
 }

 &__stars {
  margin-bottom: 20px;

  svg + svg {
   margin-left: 10px;
  }
 }

 &__name {
  font-size: 18px;
  line-height: 28px;
  margin-bottom: 10px;
 }

 &__text {
  font-size: 16px;
  line-height: 26px;
 }
}
