.modal-card {
 background: rgba(106, 106, 106, .8);
 position: fixed;
 top: 0;
 bottom: 0;
 left: 0;
 right: 0;
 z-index: 5;
 width: 100%;
 height: 100vh;
 &__modal {
  max-width: 660px;
  width: 100%;
  max-height: 660px;
  margin: 200px auto;
  text-align: center;
  padding: 60px 85px 80px;
  background: #fafbfe;
  box-shadow: 0 4px 14px 2px rgba(35, 81, 255, 0.06);
  border-radius: 40px;
 }
}
