.cases {
 margin: 0 60px;
 &__steps {
  font-size: 18px;
  line-height: 24px;
  color: #a3a8bb;
  padding: 50px 0;
  margin: 60px 0;
  border-top: 1px solid #a3a8bb;
  border-bottom: 1px solid #a3a8bb;

  .steps {
   display: flex;

   &-info {
    &__current-step {
     font-size: 44px;
     line-height: 50px;
     color: #232526;
     margin-bottom: 20px;
    }

    &__allotted-date {
     margin-bottom: 10px;
    }

    &__left-date {
     span {
      color: #232526;
     }
    }
   }

   .title-step {
    margin-left: 148px;

    &__title {
     font-size: 44px;
     line-height: 50px;
     color: #2351ff;
     margin-bottom: 20px;
    }

    &__text {
    }
   }
  }
 }
}
